.profile {
   
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 48px;
    width: 360px;
    box-shadow: 0px 8px 24px rgba(149, 157, 165, 0.2);
    border-radius: 6px;
}

.description {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: inherit;
    border-bottom: #c3c3c3 solid 1px;
}

.avatar {
    width: 132px;
        height: 132px;
        border-radius: 50%;
        overflow: hidden;
    background-color: #e7eeeeed;
    padding: 16px 8px 0px 4px;
    margin-bottom: 16px;
}

.name {
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 8px;
    color: #1c1c1c;
}

.tag {
    font-weight: 400;
    font-size: 20px;
    color: #767676;
    margin-bottom: 4px;
}

.location {
    font-weight: 400;
    font-size: 20px;
    color: #767676;
    margin-bottom: 24px;
}

.stats {
    display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        margin: auto;
        padding: 0;
        width: inherit;
        background-color: #cacaca49;
        border-radius: 0 0 6px 6px;    
}

.stats > li {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 5px;
    min-width: 80px;
    padding: 8px 0;
}



.label {
    margin-right: 3px;
    font-weight: 400;
    font-size: 16px;
    color: #767676;
    margin-bottom: 5px;
}

.quantity {
    font-weight: 800;
    font-size: 18px;
    color: #1c1c1c;
}